<template>
    <div id="app">
      <v-layout row justify-center>
        <v-dialog v-model="dialog" persistent max-width="500px">
          <v-card>
            <v-card-title>
              <span class="headline">{{dialogTitle}}</span>
            </v-card-title>
            <v-card-text>
              <v-container grid-list-md>
                <v-layout wrap>
                  <v-flex xs12>
                    <v-text-field label="" v-model="deviceInfo.Id" required></v-text-field>
                  </v-flex>
                  <v-flex xs12>
                    <v-text-field label=""  v-model="deviceInfo.username" required></v-text-field>
                  </v-flex>
                  <v-flex xs12>
                    <v-text-field label=""  v-model="deviceInfo.password" required></v-text-field>
                  </v-flex>
                  <v-flex xs12>
                    <v-text-field label=""  v-model="deviceInfo.description" required></v-text-field>
                  </v-flex> 
                  <v-flex xs12>
                    <v-text-field label=""  v-model="deviceInfo.register_date" required></v-text-field>
                  </v-flex> 
                </v-layout>
              </v-container>
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="btnClick($event)"></v-btn>
              <v-btn color="blue darken-1" text @click="btnClick($event)"></v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </v-layout>
      <v-btn color="primary" v-on:click.native="addContact()"></v-btn>
      <v-data-table :headers="headers" :items="items" class="elevation-1">
          <template v-slot:item="row">
          <tr>
              <td>{{row.item.Id}}</td>
              <td>{{row.item.username}}</td>
              <td>{{row.item.password}}</td>
              <td>{{row.item.description}}</td>
              <td>{{row.item.register_date}}</td>
              <td class="text-xs-right">
                  <v-btn color="primary" v-on:click.native="updateContact(row.item)"></v-btn>
                  <v-btn color="primary" v-on:click.native="deleteContact(row.item)"></v-btn>
                  <v-btn color="primary" v-on:click.native="sendEvent(row.item)"></v-btn>
              </td>
          </tr>
        </template>
      </v-data-table>
    </div>
  </template>
  
  <script>
  import axios from 'axios'
  export default {
    data () {
      return {
        urlinfo:'http://127.0.0.1:8000/logins',
        deviceInfo:{
          _id: null,
          Id: null,
          username:null,
          password:null,
          description:null,
          register_date: null
        },
        dialog: false,
        dialogTitle:null,
  
        headers: [
          { text: '', align: 'left', value: 'Id'},
          { text: '', align: 'left', value: 'username'},
          { text: '', align: 'left',value: 'password'},
          { text: '', align: 'left', value: 'description'},
          { text: '', align: 'left', value: 'register_date'},
          { text: '', align: 'left', value: '' },
        ],
        items:[]
      }
    },
    created(){
        axios.get(this.urlinfo,{withCredentials:true}) // 
        .then((res) => {
            console.log(res.data); //
            this.items = res.data;
        })
        .catch((err) => {
            alert(' : ' + err); // 
        });
    },
    methods:{
      addContact(){
        this.dialog = true;
        this.dialogTitle = "";
        this.deviceInfo.register_date = Date.now();
      },
      updateContact(data){
        this.dialog = true;
        this.dialogTitle = "";
        this.deviceInfo.Id = data.Id;
        this.deviceInfo.username = data.username;
        this.deviceInfo.password = data.password;
        this.deviceInfo.description = data.description;
        this.deviceInfo.register_date = data.register_date;
      },
      deleteContact(data){
        this.dialog = true;
        this.dialogTitle = "";
        this.deviceInfo.Id = data.Id;
        console.log('deleteContact : ' + data);
      },
      sendEvent(data){
        this.$bus.$emit('deviceSelected',data);
      },
  
      btnClick($event){
        this.dialog = false;
        if($event.target.innerHTML == ""){
          if(this.dialogTitle ==""){ 
            axios.post(this.urlinfo,{ Id: this.deviceInfo.Id, username: this.deviceInfo.username, password: this.deviceInfo.password, description:  this.deviceInfo.description
    , register_date:this.deviceInfo.register_date
            },{withCredentials:true}).then(() => {
              axios.get(this.urlinfo,{withCredentials:true}) // 
              .then((res) => {
                this.items = res.data;
                alert("  ");
              })
              .catch((err) => {
                alert(' : ' + err); // 
              });
              
            })
            .catch((err) => {
              alert(' : ' + err); // 
            });          
          }
          else if(this.dialogTitle==""){
            //alert("  : " + " : " + this.contactInfo.email + "  : " + this.contactInfo.password);
            axios.put(this.urlinfo + '/'+ this.deviceInfo.Id, { 
              Id: this.deviceInfo.Id, username: this.deviceInfo.username, password:  this.deviceInfo.password,
              description:  this.deviceInfo.description, 
              register_date:this.deviceInfo.register_date
            })
            .then(() => {          
              axios.get(this.urlinfo) // 
              .then((res) => {
                //console.log(res.data); //
                this.items = res.data;
                alert(" ");
              })
              .catch((err) => {
                alert(' : ' + err); // 
              });
            })
            .catch((err) => {
              alert(' : ' + err); // 
            }); 
  
          }
          else {
            axios.delete(this.urlinfo + '/'+ this.deviceInfo.Id, { data: { Id: this.deviceInfo.Id } })
            .then((result) => { 
              console.log(" " + result); //
              axios.get(this.urlinfo) // 
              .then((res) => {
                this.items = res.data;
                alert(" ");
              })
              .catch((err) => {
                alert('       : ' + err); // 
              });            
            })
            .catch((err) => { alert(' : ' + err); });          
  
          }
        }
        this.deviceInfo.Id = null;
        this.deviceInfo.username = null;
        this.deviceInfo.password = null;
        this.deviceInfo.description = null;
        this.deviceInfo.register_date = null;
      }
  
    }
  }
  </script>
  <style scoped>
    div{
      margin:20px 5px 0 5px;
    }
  </style>
  