<template>
    <div id="app">
      <v-data-table :headers="headers" :items="items" class="elevation-1">
        <template v-slot:item="row">
          <tr>
            <td>{{ row.item.name }}</td>
            <td>{{ row.item.latitude }}</td>
            <td>{{ row.item.longitude }}</td>
            <td>{{ row.item.description }}</td>
          </tr>
        </template>
      </v-data-table>
    </div>
  </template>
  
  <script>
  import axios from 'axios'
  export default {
    data() {
      return {
        urlinfo: 'http://127.0.0.1:8000/devices',
        deviceInfo: {
          _id: null,
          deviceId: null,
          name: null,
          latitude: null,
          longitude: null,
          description: null,
          register_date: null
        },
        dialog: false,
        dialogTitle: null,
  
        headers: [
          { text: '', align: 'left', value: 'name' },
          { text: '', align: 'left', value: 'latitude' },
          { text: '', align: 'left', value: 'longitude' },
          { text: '', align: 'left', value: 'description' }
        ],
        items: []
      }
    },
    created() {
      axios.get(this.urlinfo) // 
        .then((res) => {
          console.log(res.data); //
          this.items = res.data;
        })
        .catch((err) => {
          alert(' : ' + err); // 
        });
    },
    methods: {
      btnClick($event) {
        this.dialog = false;
        if ($event.target.innerHTML == "") {
          if (this.dialogTitle == "") {
            axios.post(this.urlinfo, {
              deviceId: this.deviceInfo.deviceId, name: this.deviceInfo.name, latitude: this.deviceInfo.latitude, longitude: this.deviceInfo.longitude, description: this.deviceInfo.description
              , register_date: this.deviceInfo.register_date
            })
              .then(() => {
                axios.get(this.urlinfo) // 
                  .then((res) => {
                    //console.log(res.data); //
                    this.items = res.data;
                    alert("  ");
                  })
                  .catch((err) => {
                    alert(' : ' + err); // 
                  });
  
              })
              .catch((err) => {
                alert(' : ' + err); // 
              });
          }
          else if (this.dialogTitle == "") {
            //alert("  : " + " : " + this.contactInfo.email + "  : " + this.contactInfo.password);
            axios.put(this.urlinfo + '/' + this.deviceInfo.deviceId, {
              deviceId: this.deviceInfo.deviceId, name: this.deviceInfo.name, latitude: this.deviceInfo.latitude,
              longitude: this.deviceInfo.longitude, description: this.deviceInfo.description,
              register_date: this.deviceInfo.register_date
            })
              .then(() => {
                axios.get(this.urlinfo) // 
                  .then((res) => {
                    //console.log(res.data); //
                    this.items = res.data;
                    alert(" ");
                  })
                  .catch((err) => {
                    alert(' : ' + err); // 
                  });
              })
              .catch((err) => {
                alert(' : ' + err); // 
              });
  
          }
          else {
            axios.delete(this.urlinfo + '/' + this.deviceInfo.deviceId, { data: { deviceId: this.deviceInfo.deviceId } })
              .then((result) => {
                console.log(" " + result); //
                axios.get(this.urlinfo) // 
                  .then((res) => {
                    this.items = res.data;
                    alert(" ");
                  })
                  .catch((err) => {
                    alert('       : ' + err); // 
                  });
              })
              .catch((err) => { alert(' : ' + err); });
  
          }
        }
        this.deviceInfo.deviceId = null;
        this.deviceInfo.name = null;
        this.deviceInfo.latitude = null;
        this.deviceInfo.longitude = null;
        this.deviceInfo.description = null;
        this.deviceInfo.register_date = null;
      }
  
    }
  }
  </script>
  <style scoped>
  div {
    margin: 0 5px 0 5px;
  }
  </style>
  