<script>
export default {
  data() {
    return {
      width: 460,
      height: 300,
      current: 0,
      imageSources: [
        "https://img.siksinhot.com/place/1592202548742277.jpg?w=307&h=300&c=Y",
        "https://img.siksinhot.com/place/1592202317751271.jpg?w=307&h=300&c=Y",
        "https://cdn.hellodd.com/news/photo/202104/92363_304253_5418.jpg",
        "https://ak-d.tripcdn.com/images/1i66y22348n9jov6893AE.jpg?proc=source/trip"
      ]
    };
  },
  mounted() {
    this.startSlideshow();
  },
  methods: {
    setCurrent(index) {
      this.current = index;
      this.moveTo();
    },
    startSlideshow() {
      setInterval(this.nextSlide, 3000);
    },
    moveTo() {
      this.$nextTick(() => {
        const images = this.$refs.images;
        if (images) {
          images.style.left = `-${this.current * this.width}px`;
        }
      });
    },
    prevSlide() {
      this.current =
        (this.current - 1 + this.imageSources.length) % this.imageSources.length;
      this.moveTo();
    },
    nextSlide() {
      this.current = (this.current + 1) % this.imageSources.length;
      this.moveTo();
    }
  }
};
</script>

